<template>
  <div class="yinHangRiJiZhangCatalog">
    <!--    <el-button @click="editSubject">点击切换页面</el-button>-->
    <div class="title">
      <span>银行存款日记账 - 账目目录表</span>
    </div>
    <div class="content">
      <!--头-->
      <div class="content-header flex">
        <div
          class="flex-1 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
        >科目名称</div>
        <div
          class="flex-2 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
        >二级科目名称</div>
        <div
          class="w-96 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
        >页码</div>
        <div class="flex-1 border-bottom-grey flex flex-align-center flex-justify-center bold">查看账页</div>
        <!--        <div class="flex-1 border-bottom-grey flex flex-align-center flex-justify-center bold">更改页码</div>-->
      </div>


      <div class="content-table-yinhang-rijizhang" style="background: #ffffff;">
        <div
          v-for="(item, index) in answer"
          :key="index"
          class="flex h-50 position-relative"
          :class="index + 1 === active?'bg-green':''"
          @mouseover="mouseoverLine(index)"
          @mouseleave="mouseLeaveLine"
        >
          <!--一级科目-->
          <div
            class="flex-1 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
          >
            <el-input
              v-model="item.subject_name"
              size="small"
              type="text"
              class="ml-5 mr-5"
              name="danju[]"
            ></el-input>
          </div>
          <!--二级科目-->
          <div
            class="flex-2 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
          >
            <el-input
              v-model="item.second_subject_name"
              size="small"
              type="text"
              class="ml-5 mr-5"
              name="danju[]"
            ></el-input>
          </div>
          <!--页码-->
          <div
            class="w-96 border-right-bottom-grey flex flex-align-center flex-justify-center bold"
          >
            <el-input
              v-model="item.page"
              size="small"
              type="text"
              maxlength="2"
              class="ml-5 mr-5"
              name="danju[]"
              style="position: unset;"
            ></el-input>
          </div>
          <div class="flex-1 border-bottom-grey flex flex-align-center flex-justify-center bold">
            <el-button type="text" size="small" @click="editSubject(item, index)">编辑</el-button>
          </div>
          <!--<div class="flex-1 border-bottom-grey flex flex-align-center flex-justify-center bold">
            <el-button type="text" size="small">更改页码</el-button>
          </div>-->
          <!--增行按钮-->
          <div v-show="index + 1 === active" class="add-line-icon-div">
            <i class="el-icon-circle-plus-outline add-line-icon" @click="addLine"></i>
          </div>
          <!--减少按钮-->
          <div v-show="index + 1 === active" class="remove-line-icon-div">
            <i class="el-icon-remove-outline remove-line-icon" @click="removeLine(index)"></i>
          </div>
        </div>
      </div>
    </div>

    <!-- <div class="mt-15 text-left">
      <el-button type="primary" size="small" @click="saveAnswer">保存</el-button>
    </div>-->
  </div>
</template>
<script>
export default {
  name: 'YinHangRiJiZhangCatalog',
  data () {
    return {
      subjectIndex: 0,
      active: 0
    }
  },
  props: {
    answer: {
      type: Array,
      required: true,
      default: function () {
        return []
      }
    },
    rjzNum: {
      type: Number,
      required: true,
      default: 0
    }
  },
  mounted () {

  },
  created () {

  },
  methods: {
    editSubject (item, index) {
      if (item.subject_name === '' || item.page === '') {
        this.$message.warning('请先完善当前行信息')
      } else {
        this.saveAnswer('mod')
        this.$emit('toggleClick', index)
      }
    },
    // 增行
    addLine () {
      this.answer.push({ subject_name: '', second_subject_name: '', page: '', info: [] })
    },
    // 删行 有值清空数据，没值删除行
    removeLine (index) {
      if (this.answer[index].subject_name !== '' || this.answer[index].second_subject_name !== '' || this.answer[index].page !== '') {
        this.answer[index].subject_name = ''
        this.answer[index].second_subject_name = ''
        this.answer[index].page = ''
        this.answer[index].info = []
      } else if (this.answer.length >= 2) {
        this.answer.splice(index, 1)
      }
    },
    mouseoverLine (index) {
      this.active = index + 1
    },
    mouseLeaveLine () {
      this.active = 0
    },
    // 保存答案
    saveAnswer (flag) {
      let _v = false
      for (let one of this.answer) {
        if (one.info && one.info.length > 0) {
          _v = true
          break
        }

        if (one.page) {
          _v = true
          break
        }

        if (one.subject_name) {
          _v = true
          break
        }
      }

      let _c = {
        answer: [...this.answer],
        index: this.rjzNum
      }
      if (_v) {
        this.$store.commit('SAVE_YIN_HANG_RI_JI_ZHANG_ANSWER', _c)
        this.$parent.initParent()
        if (flag !== 'mod') {
          this.$message.success('保存成功')
        }
      } else {
        this.$message.warning('请填写数据')
      }

    }
  }
}
</script>
<style>
.w-60 {
  width: 60px;
}
.ml-5 {
  margin-left: 5px;
}
.mr-5 {
  margin-right: 5px;
}
.yinHangRiJiZhangCatalog
  .content-table-yinhang-rijizhang
  .el-input
  .el-input--small
  .el-input__inner {
  position: unset !important;
}
</style>
<style lang="scss" >
.yinHangRiJiZhangCatalog {
  width: 100%;
  background: #fff;
  margin: 0 auto;
  color: #333333;
  padding: 20px;
  .title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1px;
    position: relative;
  }
  .content {
    input[name="danju[]"] {
      position: unset !important;
      border: 1px solid #dcdfe6 !important;
      background: transparent;
    }
    margin-top: 35px;
    border-style: solid;
    border-color: #e8e8e8;
    border-width: 1px 1px 0 1px;
    .content-header {
      width: 100%;
      height: 40px;
      font-size: 13px;
      background: #f3f3f3;
    }
    .bg-green {
      background-color: #e5f7d6;
    }
    .add-line-icon-div {
      position: absolute;
      height: 50px;
      line-height: 50px;
      width: 30px;
      left: -30px;
      text-align: center;
      top: 0;
    }
    .add-line-icon {
      font-size: 20px;
      color: #6dc3ea;
      cursor: pointer;
    }
    .remove-line-icon-div {
      position: absolute;
      height: 50px;
      line-height: 50px;
      text-align: center;
      width: 30px;
      right: -30px;
      top: 0;
    }
    .remove-line-icon {
      font-size: 20px;
      color: #f74c23;
      cursor: pointer;
    }
  }
}
</style>
