<template>
  <div class="keMuHuiZongBiao">
    <div class="title">
      <div style="color:#666666;">{{ name }}</div>
      <div class="font-13 mt-15">
      </div>
    </div>
 
    <div class="content">
      <!--头-->
      <div class="content-header flex">
        <!--会计科目-->
        <div
          class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey"
          v-for="(item,index) of tableHeader"
          :key="index"
          style="border-style: unset;border-width: 0px;border-right-width: 1px;"
          :style="index === tableHeader.length - 1 ? '' : 'border-right: 1px solid #DCDFE6 !important;'"
        >{{ item.txt }}</div>
      </div>
      <!--table-->
      <div class="content-table">
        <div
          v-for="(item, index) in tableDataList"
          :key="index"
          :class="['flex', 'h-36', 'position-relative', index + 1 === active?'bg-green':'']"
          @mouseover="mouseoverLine(index)"
          @mouseleave="mouseLeaveLine"
          style="border-top: 1px solid #DCDFE6 !important;"
        >
          <div
            class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey;"
            v-for="(item_line,index_one,keyIndex) of item"
            :key="index_one"
            :style="index_one === tableHeader[tableHeader.length - 1].col ? '' : 'border-right: 1px solid #DCDFE6 !important;'"
          >
            <!-- <input v-if="item_line.show" @click="item_line.show=false" name="danju[]" style="font-size: 12px;" v-model="item_line.content" /> -->
              <!-- {{item_line.content}} -->
              <!-- <i
                class="el-icon-edit-outline"
                @click="item_line.show=false"
              ></i> -->
            <!-- </input> -->
            <!-- 失去焦点时更改"show"属性，显示文本 -->
            <el-input name="danju[]" v-model="tableHeader[index][keyIndex]" type="text" size="small" @blur="item_line.show=true" autofocus style="margin-top: -30px;" />

            <!-- <span>{{ item.prop }}</span>
            <el-input v-model="item_line.prop" type="text" size="small" /> -->
          </div>

          <!--增行按钮-->
          <div v-show="index + 1 === active" class="add-line-icon-div">
            <i class="el-icon-circle-plus-outline add-line-icon" @click="addLine('')"></i>
          </div>
          <!--减少按钮-->
          <div v-show="index + 1 === active" class="remove-line-icon-div">
            <i class="el-icon-remove-outline remove-line-icon" @click="removeLine(index)"></i>
          </div>
        </div>
      </div>
    </div>

    <!-- <div class="mt-15 text-left">
      <el-button type="primary" size="small" @click="saveAnswer">保存</el-button>
    </div> -->
  </div>
</template>

<script>

export default {
  name: 'KeMuHuiZongBiao',
  components: {

  },
  data () {
    return {
      tableDataList: [],
      active: 0
    }
  },
  props: {
    name: {
      type: String,
      default: '表格'
    },
    tableHeader: {
      type: Array,
      default: function () {
        return []
      }
    },
    tableData: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  watch: {
    tableData () {
      this.init()
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.tableDataList = []

      if (this.tableData.length === 0) {
        this.addLine()
      } else {
        this.addLine(this.tableData.length / this.tableHeader.length)
      }
    },
    // 增行
    addLine (num) {
      if (num) {
        for (let i = 0;i < num;i ++) {
          let _v = {}
          for (const one of this.tableHeader) {
            _v[one.col] = { content: '', show: true }
          }
          this.tableDataList.push(_v)
        }
      } else{
        let _v = {}
        for (const one of this.tableHeader) {
          _v[one.col] = { content: '', show: true }
        }
        this.tableDataList.push(_v)
      }
    },
    // 删行
    removeLine (index) {
      if (this.tableDataList.length >= 2) {
        this.tableDataList.splice(index, 1)
      }
      // 删除数组对应数据
      this.$parent.delTableData(index);

      // if (this.initData.table[index].zi_chan_subject !== '' || this.initData.table[index].zi_chan_jie_money !== '' || this.initData.table[index].zi_chan_dai_money !== '' || this.initData.table[index].fu_zhai_subject !== '' || this.initData.table[index].fu_zhai_jie_money !== '' || this.initData.table[index].fu_zhai_dai_money !== '') {
      //   this.initData.table[index].zi_chan_subject = ''
      //   this.initData.table[index].zi_chan_jie_money = ''
      //   this.initData.table[index].zi_chan_dai_money = ''
      //   this.initData.table[index].fu_zhai_subject = ''
      //   this.initData.table[index].fu_zhai_jie_money = ''
      //   this.initData.table[index].fu_zhai_dai_money = ''
      // } else if (this.initData.table.length >= 2) {
      //   this.initData.table.splice(index, 1)
      // }
    },
    mouseoverLine (index) {
      this.active = index + 1
    },
    mouseLeaveLine () {
      this.active = 0
    },
    // 保存科目汇总表答案
    // saveAnswer () {
      // this.$store.commit('SAVE_KE_MU_HUI_ZONG_ANSWER', [...this.tableData])
      // this.$store.commit('save_' + type, this.tableData)
      // this.$message.success('保存成功')
    // }
  }
}
</script>
<style>
.ml-5 {
  margin-left: 5px;
}
.pl-10 {
  padding-left: 10px;
}
.pr-10 {
  padding-right: 10px;
}
.w-50 {
  width: 50px;
}
.w-60 {
  width: 60px;
}
.w-70 {
  width: 70px;
}
.w-160 {
  width: 160px;
}
.h-36 {
  height: 36px;
}
.h-30 {
  height: 30px;
}
.line-height-30 {
  line-height: 30px;
}
.content-header .el-input__inner {
  padding: 0;
  background-color: transparent;
  border: none;
}
.content-table .el-input__inner {
  padding: 0 5px;
  border: none;
  text-align: left;
  background-color: transparent;
  font-size: 12px;
}
.content-table .center .el-input__inner {
  text-align: center;
  font-size: 12px;
}
.content-table .el-input--small {
  border-radius: 0px;
  font-size: 12px;
}
</style>
<style lang="scss" scoped>
.keMuHuiZongBiao {
  // min-width: 1000px;
  // width: 1000px;
  margin: 0 auto;
  color: #333333;
  padding: 20px;
  .title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1px;
    position: relative;
    .page {
      position: absolute;
      right: 0;
      top: 12px;
    }
  }
  .content {
    margin-top: 15px;
    border-style: solid;
    border-color: #e8e8e8;
    border-width: 1px 1px 0 1px;
    border: 1px solid #666666;
    .content-header {
      width: 100%;
      height: 45px;
      background: #f3f3f3;
      font-size: 16px;
      color: #666666;
    }
    .content-table {
      .bg-green {
        background-color: #e5f7d6;
      }

      .add-line-icon-div {
        position: absolute;
        height: 36px;
        line-height: 36px;
        width: 30px;
        left: -30px;
        text-align: center;
      }

      .add-line-icon {
        font-size: 20px;
        color: #6dc3ea;
        cursor: pointer;
      }
      .remove-line-icon-div {
        position: absolute;
        height: 36px;
        line-height: 36px;
        text-align: center;
        width: 30px;
        right: -30px;
      }
      .remove-line-icon {
        font-size: 20px;
        color: #f74c23;
        cursor: pointer;
      }
    }
  }
}
</style>
