<template>
  <div class="yinhangRiJiZhang">
    <YinHangRiJiZhangCatalog v-if="page === 1" :answer="riJiZhangAnswer" :rjzNum="rjzNum" @toggleClick="editSubject" ref="cataSave" />
    <YinHangRiJiZhangDetail v-else :subject-index="tableIndex" :rjzNum="rjzNum" ref="detailSave" @toggleClick="backToSubject"/>
  </div>
</template>

<script>
import YinHangRiJiZhangCatalog from './YinHangRiJiZhangCatalog'
import YinHangRiJiZhangDetail from './YinHangRiJiZhangDetail'
export default {
  name: 'YinhangRiJiZhang',
  data () {
    return {
      page: 1,
      tableIndex: 0
    }
  },
  props: {
    riJiZhangAnswer: {
      type: Array,
      required: true,
      defaults: []
    },
    rjzNum: {
      type: Number,
      required: true,
      default: 0
    }
  },
  components: { YinHangRiJiZhangCatalog, YinHangRiJiZhangDetail },
  methods: {
    // 切换到详情页
    editSubject (index) {
      this.tableIndex = index
      this.page = 2
    },
    // 切换到科目填写页
    backToSubject () {
      this.$parent.rjzInit()
      this.page = 1
    },
    checkSave () {
      if (this.page === 1) {
        this.$refs.cataSave.saveAnswer()
      } else {
        this.$refs.detailSave.saveChildAnswer()
      }
    },
    initParent () {
      this.$parent.initYhckrjz()
    }
  }
}
</script>
