<style lang="scss">
#djk {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .tool {
    height: 40px;
    padding: 0 10px;
    border-bottom: 1px solid rgb(219, 219, 219);

    .tool-search {
      position: relative;

      svg {
        position: absolute;
        top: 5px;
        left: 5px;
      }

      input {
        height: 22px;
        padding-right: 22px;
        text-indent: 24px;
        color: rgb(65, 80, 88);
        background: rgb(255, 255, 255);
        border-width: 1px;
        border-style: solid;
        border-color: rgb(219, 219, 219);
        border-radius: 2px;
        transition: all 0.2s ease-out 0s;
      }
    }
  }

  .content {
    overflow: hidden;

    .left-side {
      width: 150px;
      height: 100%;
      overflow: auto;
      border-right: 1px solid #dbdbdb;

      ul {
        padding: 5px 10px;
        box-sizing: border-box;
        width: 100%;
        overflow: hidden;

        li {
          cursor: pointer;
          height: 25px;
          font-size: 12px;
          background: #fff;

          span.active {
            color: #298df8;
          }
        }
      }

      .svg-icon {
        flex-shrink: 0;
        margin-right: 4px;
        width: 14px;

        &.active {
          fill: #298df8;
        }
      }
    }

    .right-side {
      width: 0;
      padding: 20px;
      overflow: auto;
      flex-wrap: wrap;
      align-content: flex-start;

      .item {
        box-sizing: border-box;
        font-size: 12px;
        width: 120px;
        height: 100px;
        margin: 10px;
        overflow: hidden;
        cursor: pointer;
        position: relative;

        img {
          display: block;
          box-sizing: border-box;
          border: 2px solid #fff;
          width: 120px;
          height: 60px;
          overflow: hidden;

          &.active {
            border: 2px solid rgb(121, 186, 255);
            position: relative;
            filter: brightness(90%);
          }
        }

        span {
          width: 120px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          display: inline-block;
          height: 20px;
          line-height: 20px;
          text-align: center;
          margin-top: 5px;
        }
      }
    }
  }

  .bottom {
    border-top: 1px solid rgb(219, 219, 219);
    justify-content: flex-end;
    padding: 0 20px;
    height: 40px;

    button {
      cursor: pointer;
      min-width: 68px;
      height: 24px;
      margin-left: 10px;
      color: rgb(65, 80, 88);
      padding: 0 16px;
      background: rgb(255, 255, 255);
      border-width: 1px;
      border-style: solid;
      border-color: rgb(200, 205, 208);
      border-image: initial;
      border-radius: 2px;
      transition: all 0.2s ease-in-out 0s;

      &:hover {
        background: rgb(141, 158, 167);
      }
    }
  }
}
</style>
