        <div class="tips" style="width: 100%;">
          从单据库中
          <span class="link" @click="djkDialogVisible=true">选择一份单据</span> 吧
        </div>
      </div>

      <div v-else id="container" class="flex-1">
        <div class="main" ref="main">
          <!-- 记账凭证 -->
          <div v-if="jzpzDialogVisible">
            <div>
              <img :src="pic" class="imgBack" @load="positionSet" style="display:none;" />
              <form ref="danJuForm">
                <div class="layout">
                  <jzpz-zu :index="0" :pingZhengAnswer="pingZhengAnswer" @initClick="initBefore" ref="jzpz"></jzpz-zu>
                </div>
              </form>
            </div>
          </div>

          <!-- 现金日记账 -->
          <div v-if="xjrjzDialogVisible">
            <div>
              <img :src="pic" class="imgBack" @load="positionSet" style="display:none;" />
              <form ref="danJuForm">
                <div class="layout">
                  <xianjinrijizhang :riJiZhangAnswer="xjrjzData"></xianjinrijizhang>
                </div>
              </form>
            </div>
          </div>

          <!-- 银行存款日记账 -->
          <div v-if="yhckrjzDialogVisible">
            <div>
              <img :src="pic" class="imgBack" @load="positionSet" style="display:none;" />
              <form ref="danJuForm">
                <div class="layout">
                  <yinhangrijizhang :riJiZhangAnswer="riJiZhangAnswer" :rjzNum="rjzNum" ref="yhckrjz"></yinhangrijizhang>
                </div>
              </form>
            </div>
          </div>

          <!-- 资金报表 -->
          <div v-if="zjbbDialogVisible">
            <div>
              <img :src="zjbbPic" class="imgBack" @load="imageLoaded" />
              <form ref="danJuForm">
                <div class="layout" v-html="html"></div>
              </form>
            </div>
          </div>

          <!-- 单据库--单条 -->
          <div
            v-if="!xjrjzDialogVisible &&
                  !yhckrjzDialogVisible &&
                  !zjbbDialogVisible &&
                  !bwbDialogVisible &&
                  !djkDialogVisible &&
                  !jzpzDialogVisible &&
                  danju.srcDataArr &&
                  danju.srcDataArr.length === 1 &&
                  danju.type !== 'bcb_cdhp' &&
                  danju.type != 'bcb_djjj' &&
                  danju.type !== 'bcb_xjzp' &&
                  danju.type !== 'bcb_ygjk' &&
                  danju.type !== 'bcb_zzzp'"
          >
            <div v-for="(item,index) of danju.srcDataArr" :key="index">
              <img :src="item.src" :alt="danju.label" @load="imageLoaded" class="imgBack" />
              <form ref="danJuForm">
                <div class="layout" v-html="item.data"></div>
              </form>
            </div>
          </div>

          <!-- 备查簿 -->
          <div
            v-if="!xjrjzDialogVisible &&
                  !yhckrjzDialogVisible &&
                  !zjbbDialogVisible &&
                  !bwbDialogVisible &&
                  !djkDialogVisible &&
                  !jzpzDialogVisible &&
                  (danju.type === 'bcb_cdhp' || danju.type === 'bcb_djjj' || danju.type === 'bcb_xjzp' || danju.type === 'bcb_ygjk' || danju.type === 'bcb_zzzp')"
          >
            <div v-for="(item,index) of danju.srcDataArr" :key="index">
              <img
                :src="item.src"
                :alt="danju.label"
                @load="imageLoaded"
                class="imgBack"
                style="display:none;"
              />
              <form ref="danJuForm">
                <div class="layout">
                  <addDelTable
                    v-if="danju.srcDataArr && danju.srcDataArr[0] && danju.srcDataArr[0].data"
                    :tableHeader="danju.srcDataArr[0].data.tableHeader"
                    :tableData="tableData"
                    :name="danju.label"
                    ref="bcbZuJian"
                  ></addDelTable>
                </div>
              </form>
            </div>
          </div>
          <!-- 单据库--多条 -->
          <div
            v-if="!xjrjzDialogVisible &&
                    !yhckrjzDialogVisible &&
                    !zjbbDialogVisible &&
                    !bwbDialogVisible &&
                    !djkDialogVisible &&
                    !jzpzDialogVisible &&
                    danju.srcDataArr &&
                    danju.srcDataArr.length > 1 &&
                    danju.type !== 'bcb_cdhp' &&
                    danju.type != 'bcb_djjj' &&
                    danju.type !== 'bcb_xjzp' &&
                    danju.type !== 'bcb_ygjk' &&
                    danju.type !== 'bcb_zzzp'"
            style="margin-top: -38px;"
          >
            <el-button-group style="margin-bottom: 10px;">
              <el-button type="primary" size="mini" @click="changePage(index)" v-for="(item,index) of danju.srcDataArr" :key="index">{{ item.label }}</el-button>
            </el-button-group>
            <div v-for="(item,index) of danju.srcDataArr" :key="index">
              <img :src="item.src" :alt="danju.label" @load="imageLoaded" class="imgBack" v-show="index === curIndex" />
              <form ref="danJuForm" v-show="index === curIndex">
                <div class="layout" v-html="item.data"></div>
              </form>
            </div>
          </div>

          <!-- 印章库 -->
          <div v-if="!yzkDialogVisible && yzData && yzData.length > 0" id="picMove">
            <div v-for="(item,index) of yzData[curIndex]" :key="index" :id="'picMove_' + index"
              :style="{'width': item.width,'height':item.height,'left':item.left,'top':item.top}"
              @mouseover="mouseOver(index)" @mouseleave="mouseLeave(index)" @click="btnclick('picMove_' + index,index)" style="position: absolute;z-index:20000;">
              <!-- <span v-show="item.show && closeShow" class="close" @click.stop="contentDel(index)" /> -->
              <span v-show="item.show" class="close" @click.stop="contentDel(index)" />

              <!-- <img :src="item.src" :alt="item.label" class="imgBack" :style="closeShow ? 'cursor: move;' : ''" style="width:90px;" /> -->
              <img :src="item.src" :alt="item.label" class="imgBack" style="width:90px;" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 底部区域-->
    <div class="footer flex" :style="!hide ?  'animation-name: goIn' : 'animation-name: goOut'">
      <div class="flex flex-1 drawer">
        <div class="flex flex-1 flex-around">
          <!-- 账本-->
          <div class="tool-item flex flex-column" @click="jzpzCon">
            <i>
              <img :src="jzpz" alt style="width:35px;" />
            </i>
            <span class="label">记账凭证</span>
          </div>
          <div class="tool-item flex flex-column" @click="xjrjz">
            <i>
              <img :src="xjjz" alt style="width:35px;" />
            </i>
