        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 177px;top: 607px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 281px;top: 607px;width: 103px;;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 385px;top: 607px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 489px;top: 607px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 593px;top: 607px;width: 107px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 177px;top: 636px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 281px;top: 636px;width: 103px;;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 385px;top: 636px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 489px;top: 636px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 593px;top: 636px;width: 107px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 177px;top: 665px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 281px;top: 665px;width: 103px;;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 385px;top: 665px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 489px;top: 665px;width: 103px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 593px;top: 665px;width: 107px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 112px;top: 696px;width: 190px;height: 28px;">' +
        '<input type="text" name="danju[]" class="input-text" autocomplete="off" style="left: 348px;top: 696px;width: 190px;height: 28px;;">',
      zjbbDialogVisible: false,
      zshdDialogVisible: false,
      yhckrjzDialogVisible: false,
      xjrjzDialogVisible: false,
      djkDialogVisible: false,
      stampDialogVisible: false,
      bwbDialogVisible: false,
      jzpzDialogVisible: false,
      yzkDialogVisible: false,
      hide: false,
      danju: null,
      yinzhangDtat: '',
      desktopImageStack: [],
      dataList: [],
      yzData: [
        [],
        []
      ]
    }
  },
  mounted () {
    this.$nextTick(() => {
      console.log('出纳系统注册消息成功')
      window.addEventListener('message', this.handleMessage)
    })
  },
  destroyed () {
    window.removeEventListener('message', this.handleMessage)
  },
  watch: {
    yinzhangDtat () {
      this.yinzhangData()
    }
  },
  computed: {

  },
  created () {

  },
  methods: {
    sendMessage (cmd, params) {
      window.parent.postMessage({ cmd, params }, '*')
    },
    handleMessage (msg) {
      const { data } = msg
      if (data.cmd === 'getAnswer') {
        const answer = this.$store.state.answer
        const keys = Object.keys(answer)
        if (keys.length > 0) {
          this.sendMessage('echo', { business_id: data.params.business_id, answer: answer })
          this.$store.commit('resetAnswer')
        }
      }
    },
    initYhckrjz () {
      this.riJiZhangAnswer = [{ subject_name: '', second_subject_name: '', page: '', info: [] }]
    },
    yinzhangData () {
      this.yzData[this.curIndex].push(this.yinzhangDtat)
    },
    btnclick(id,index) {
      var obj = document.getElementById(id)
      var obox = document.getElementById('containBox')
      this.drag(obj, obox,index)
    },
    drag(box, obox,index) {
      var disX
      var disY
      var that = this
      box.onmousedown = function(ev) {
        var oEvent = ev || event
        disX = oEvent.clientX - box.offsetLeft
        disY = oEvent.clientY - box.offsetTop
        that.isDrag = true
        document.onmousemove = function(ev) {
          if (that.isDrag) {
            var oEvent = ev || event
            var l = oEvent.clientX - disX
            var t = oEvent.clientY - disY
            if (l < 0) {
              l = 0
            }
            if (t < 0) {
              t = 0
            }
            if (l > obox.clientWidth - box.clientWidth) {
              l = obox.clientWidth - box.clientWidth
            }
            if (t > obox.clientHeight - box.clientHeight) {
              t = obox.clientHeight - box.clientHeight
            }
            box.style.left = l + 'px'
            box.style.top = t + 'px'
          }
        }
        document.onmouseup = function() {
          that.isDrag = false
          document.onmousemove = null
          document.onmouseup = null
          that.savePosition(box,index)
        }
        return false
      }
    },
    savePosition (box,index) { // 保存当前位置
      this.yzData[this.curIndex][index].left = box.style.left
      this.yzData[this.curIndex][index].top = box.style.top
    },
    mouseOver(index) {
      this.yzData[this.rjzNum][index].show = true
    },
    mouseLeave(index) {
      this.yzData[this.rjzNum][index].show = false
    },
    contentDel(index) {
      this.yzData.splice(index,1) //需要调试，很可能不行
      // this.yzData[this.rjzNum].splice(index,1)
    },
    hasDone () {
      this.drawer = true
    },
    handleClose (done) {
      done();
      // this.$confirm('确认关闭？')
      //   .then(() => {
      //     done();
      //   })
      //   .catch(() => { });
    },
    delTableData (index) {
      let _arr = []
      for (let one in this.$store.state.answer.bcb[this.danju.type][0].formdata[0]) {
        if (one < this.danju.srcDataArr[0].data.tableHeader.length * index || one >= this.danju.srcDataArr[0].data.tableHeader.length * (index + 1)) {
          _arr.push(this.$store.state.answer.bcb[this.danju.type][0].formdata[0][one])
        }
      }

      this.$store.state.answer.bcb[this.danju.type][0].formdata[0] = _arr
      this.getOneDj(this.rjzNum)
    },
    getOneDj (index) {
      console.log(this.danju)
      this.rjzNum = index
      this.recoverData(index)
    },
    getDataList () {
      if (this.danju) {
        if (this.danju.category !== 'yzk') {
          this.dataList = this.$store.state.answer[this.danju.category][this.danju.type]
        }
      } else if (!this.danju && this.zjbbDialogVisible) {
        this.dataList = this.$store.state.answer.zjbb
      } else if (!this.danju && this.xjrjzDialogVisible) {
        this.dataList = this.$store.state.answer.xjrjz
      } else if (!this.danju && this.yhckrjzDialogVisible) {
        this.dataList = this.$store.state.answer.yhckrjz
      } else if (!this.danju && this.jzpzDialogVisible) {
        this.dataList = this.$store.state.answer.pingZheng
      }
    },
    saveDanju () {  // 存储数据
      let form = this.$refs['danJuForm']

      let formdata = ''
      let _v = []
      if (this.danju) {
        for (let one in form) {
          _v.push(new FormData(form[one]))
        }
        formdata = _v
      } else {
        _v.push(new FormData(form))
        formdata = _v
      }

      if (this.danju) {
        // if (this.danju.category === 'bcb') {
        //   this.$refs.bcbZuJian[0].saveAnswer(this.danju.type)
        // } else {
          if (this.checkNull(formdata)) {
            let _vAll = {
              formdata: formdata,
              yz: this.yzData
            }

            this.$store.commit('save_' + this.danju.type, _vAll)
            this.$message.success('保存成功')
          } else {
            this.$message.warning('请填写数据')
          }
        // }
      } else if (!this.danju && this.xjrjzDialogVisible) {  // 现金日记账
        if (this.checkNull(formdata)) {
          this.$store.commit('save_xjrjz', formdata)
          this.$message.success('保存成功')
        } else {
          this.$message.warning('请填写数据')
        }
      } else if (!this.danju && this.zjbbDialogVisible) { // 资金报表
        if (this.checkNull(formdata)) {
          this.$store.commit('save_zjbb', formdata)
          this.$message.success('保存成功')
        } else {
          this.$message.warning('请填写数据')
        }
      } else if (!this.danju && this.yhckrjzDialogVisible) { // 银行存款日记账
        this.$refs.yhckrjz.checkSave()
        this.rjzNum = -1
      } else if (!this.danju && this.jzpzDialogVisible) {
        this.$refs.jzpz.saveAnswer()
      }

      this.resetData()
      this.yzData = []
    },
    checkNull (formdata) {
      let _v = false
      for (let one of formdata) {
        for (let two of one.getAll('danju[]')) {
          if (two !== '') {
            _v = true
            break
          }
        }
      }

      return _v
    },
    resetData () {  // 重置数据
      let inputs = document.getElementsByTagName('input')

      if (inputs.length > 0) {
        inputs.forEach(input => {
          if (input.name === 'danju[]') {
            input.value = ''
          }
        })
      }
    },
    initBefore () {
      this.curIndex = 0
      this.btnShow = true

      this.getDataList()
    },
    recoverData (index) {
      let inputs = document.getElementsByTagName('input')
      let _num = 0

      if (this.danju) {
        if (this.danju.category === 'bcb') {
          this.tableData = this.$store.state.answer[this.danju.category][this.danju.type][index].formdata[0]

          let that = this
          let t
          clearTimeout(t)
          t = setTimeout(function() {
            let _arr = []
            for (let one of that.$store.state.answer[that.danju.category][that.danju.type][index].formdata) {
              _arr.push.apply(_arr,one)
            }

            if (inputs.length > 0) {
              inputs.forEach(input => {
                if (input.name === 'danju[]') {
                  if (_arr[_num] === undefined) {
                    _arr[_num] = ''
                  }
                  input.value = _arr.length === 0 ? '' : _arr[_num]
                  _num = _num + 1
                }
              })
            }
          }, 50)
        } else {
          let _arr = []
          for (let one of this.$store.state.answer[this.danju.category][this.danju.type][index].formdata) {
            _arr.push.apply(_arr,one)
          }

          if (inputs.length > 0) {
            inputs.forEach(input => {
              if (input.name === 'danju[]') {
                if (_arr[_num] === undefined) {
                  _arr[_num] = ''
                }
                input.value = _arr.length === 0 ? '' : _arr[_num]
                _num = _num + 1
              }
            })
          }

          this.yzData = this.$store.state.answer[this.danju.category][this.danju.type][index].yz
        }
      } else if (!this.danju && this.zjbbDialogVisible) {
        if (inputs.length > 0) {
          inputs.forEach(input => {
            if (input.name === 'danju[]') {
              input.value = this.$store.state.answer.zjbb[index][0][_num] === undefined ? '' : this.$store.state.answer.zjbb[index][0][_num]
              _num = _num + 1
            }
          })
        }
      } else if (!this.danju && this.xjrjzDialogVisible) {
        if (inputs.length > 0) {
          inputs.forEach(input => {
            if (input.name === 'danju[]') {
              input.value = this.$store.state.answer.xjrjz[index][_num] === undefined ? '' : this.$store.state.answer.xjrjz[index][_num]
              _num = _num + 1
            }
          })
        }
      } else if (!this.danju && this.yhckrjzDialogVisible) {
        if (this.$store.state.answer.yhckrjz && this.$store.state.answer.yhckrjz[index]) {
          this.riJiZhangAnswer = this.$store.state.answer.yhckrjz[index]
        }
      } else if (!this.danju && this.jzpzDialogVisible) {
        if (this.$store.state.answer.pingZheng && this.$store.state.answer.pingZheng[index]) {
          this.pingZhengAnswer = this.$store.state.answer.pingZheng[index]
        }
      }

    },
    imageLoaded (e) {
      // 计算位置
      // this.$refs['main'].style.left = (window.innerWidth - e.target.width) / 2 + 'px'
      this.$refs['main'].style.left = (window.innerWidth - e.target.width) / 2 + 'px'
      // if (this.zjbbDialogVisible) {
      //   this.$refs['main'].style.top = (window.innerHeight - e.target.height) / 2 + 'px'
      // } else {
      this.$refs['main'].style.top = 50 + 'px'
      // }

      this.$refs['main'].style.width = e.target.width + 'px'
      this.$refs['main'].style.height = e.target.height + 'px'
    },
    positionSet () {
      this.$refs['main'].style.left = (window.innerWidth - 1200) / 2 + 'px'
      this.$refs['main'].style.top = 70 + 'px'
      this.$refs['main'].style.width = 1200 + 'px'
      this.$refs['main'].style.height = 600 + 'px'
    },
    changePage (index) {
      this.curIndex = index
      // if (this.curIndex === 0 || this.curIndex === '0') {
      //   this.closeShow = true
      // } else {
      //   this.closeShow = false
      // }

      if (this.curIndex === 0 || this.curIndex === '0') {
        return false
      }

      if (this.danju.type === 'cash_node' || this.danju.type === 'triple_receipt' || this.danju.type === 'transfer_voucher' || this.danju.type === 'collection') {

        if (this.$store.state.answer[this.danju.category][this.danju.type].length > 0) {
          this.yzData = this.$store.state.answer[this.danju.category][this.danju.type][this.rjzNum].yz
        }

        let _valInput = []

        let inputs = document.getElementsByTagName('input')

        if (inputs.length > 0) {
          inputs.forEach(input => {
            if (input.name === 'danju[]') {
              _valInput.push(input)
            }
          })
        }

        let _num = 0
        let _input = ''
        let _inputClass = ''
        if (_valInput.length > 0) {
          _valInput.forEach(input => {

            if (_num < _valInput.length / this.danju.srcDataArr.length) {
              _input = input.value
              _inputClass = input.className

              if (_input !== undefined && _input !== 'undefined') {

                let _c = document.getElementsByClassName(_inputClass)

                for (let one of _c) {
                  one.value = _input
                }
              }
            }
          })
        }
      }
    },
    jzpzCon () {
      this.closeNow()
      this.jzpzDialogVisible = true
      this.initBefore()
    },
    zjbb () {
      this.closeNow()
      this.zjbbDialogVisible = true
      this.initBefore()
    },
    zshd () {
      this.closeNow()
      this.zshdDialogVisible = true
    },
    djk () {
      this.closeNow()
      this.djkDialogVisible = true
    },
    bcb () {
      this.closeNow()
      this.bwbDialogVisible = true
    },
    xjrjz () {
      this.closeNow()
      this.xjrjzDialogVisible = true
      this.initBefore()
    },
    yhckrjzCon () {
      this.closeNow()
      this.yhckrjzDialogVisible = true
      this.initBefore()
    },
    yzk () {
      this.yzkDialogVisible = true
    },
    closeNow () {
      this.rjzNum = -1
      this.initYhckrjz()
      this.yzkDialogVisible = false

      this.yzData = [
        [],
        []
      ]
      console.log(this.$store)
      this.btnShow = false
      this.danju = null
      this.yhckrjzDialogVisible = false
      this.xjrjzDialogVisible = false
      this.bwbDialogVisible = false
      this.zjbbDialogVisible = false
      this.djkDialogVisible = false
      this.jzpzDialogVisible = false
    },
    rjzInit () {
      this.rjzNum = -1
    }
  }
}
</script>

<style lang="scss">
@import "./assets/css/common.css";

$primaryFontColor: #415058;
$primaryFontSize: 14px;

html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#app {
  min-width: 1024px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: $primaryFontSize;
  color: $primaryFontColor;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #e8e8e9;

  .el-drawer__wrapper{
    // 去掉谷歌浏览器，选中时带着边框问题
    :focus {
      outline: 0;
    }
    height: calc(100% - 50px);
    margin-top: 50px;
    .el-drawer__header{
      font-size: 16px;
    }
  }


  .top {
    position: absolute;
    box-sizing: border-box;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: #f2f2f2;
    font-size: 14px;
    padding: 0 20px;
    justify-content: space-between;
    box-shadow: rgba(100, 100, 100, 0.18) 0 2px 3px 0;

    h1 {
      text-align: left;
      font-weight: bold;
    }

    .config {
      width: 500px;
      font-size: 14px;
      font-weight: bold;
      text-align: right;
    }
  }

  .placeholder {
    width: 250px;
    height: 250px;
    text-align: center;
    margin: auto;
    color: #999;
    overflow: hidden;
    position: relative;

    .tips {
      line-height: 50px;
    }
  }

  #container {
    position: relative;
    // padding-top: 51px;
    top: 51px;
    overflow-y: auto;
    max-height: 600px;
    max-height: 80vh;
    .main {
      position: absolute;
      // max-height: 600px;
      // overflow-y: auto;

      form {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
        width: 100%;
        height: 100%;
      }

      .imgBack {
        z-index: -1;
      }

      .layout {
        position: relative;
        left: 0;
        top: 0;
        z-index: 11;
        width: 100%;
        height: 100%;
        // max-height: 650px;
        // overflow-y: auto;

        input[name="danju[]"] {
          position: absolute;
          border: none;
          background: transparent;
          // background: red;
          font-size: 14px;
        }
      }
      #picMove :hover{
        cursor: move;
        .close {
          background: #409EFF;
          color: white;
          border-radius: 12px;
          line-height: 20px;
          text-align: center;
          height: 20px;
          width: 20px;
          font-size: 18px;
          padding: 1px;
          top: -10px;
          right: -10px;
          position: absolute;
          z-index: 1;
          cursor: pointer;
        }

        .close::before {
          content: "\2716";
        }
      }
    }
  }

  .footer {
    animation-duration: 0.5s;
    animation-direction: alternate;
    animation-fill-mode: forwards;
    margin: 0 auto;
    flex-shrink: 0;
    position: relative;
    background: #f2f2f2;
    padding: 5px;
    box-shadow: rgba(100, 100, 100, 0.18) 2px 2px 3px 0;

    .drawer {
      justify-content: space-around;

      .line {
        width: 1px;
        height: 30px;
        margin: auto 10px;
        background: #ddd;
      }

      .tool-item {
        width: 95px;
        height: 70px;
        padding: 0 10px;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }

      .tool-item:hover {
        transform: translateY(-10px);
        transition: 0.2s;
      }

      .tool-item .label {
        width: 90px;
        height: 22px;
        line-height: 22px;
        text-align: center;
        font-weight: bold;
        font-size: 12px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #415058;
      }
    }
  }

  .rightContaoner {
    width: 250px;
    height: 90vh;
    position: absolute;
    right: 10px;
    top: 60px;
    margin-bottom: 16px;
    padding: 16px;
    background: #ffffff;
    overflow-y: auto;
    z-index: 1000;
    .itemStyle {
      padding: 13px 20px;
      position: relative;
      background: #f6f7fa;
      margin-bottom: 10px;
      cursor: pointer;
      border-radius: 5px;
    }
    .itemStyle:hover {
      opacity: 0.85;
      color: #298df8;
    }
  }

  #mask {
    animation-duration: 0.2s;
    animation-direction: alternate;
    animation-fill-mode: forwards;
    position: absolute;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);

    #dialog {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 1000px;
      height: 600px;
      border-radius: 3px;
      background: #ffffff;

      .title {
        padding: 0 20px;
        height: 40px;
        border-bottom: 1px solid rgb(219, 219, 219);
      }

      .main {
        overflow: hidden;
      }
    }
  }
}

@keyframes goIn {
  from {
    /*0%*/
    transform: translateY(100px);
  }
  to {
    /*100%*/
    transform: translateY(0);
  }
}

@keyframes goOut {
  from {
    /*0%*/
    transform: translateY(0);
  }
  to {
    /*100%*/
    transform: translateY(100px);
  }
}

@keyframes scaleIn {
  from {
    /*0%*/
    transform: scale(1.2);
  }
  to {
    /*100%*/
    transform: scale(1);
  }
}

@keyframes scaleOut {
  from {
    /*0%*/
    opacity: 1;
  }
  to {
    /*100%*/
    opacity: 0.5;
  }
}
</style>

