<template>
  <div id="yzk" class="flex flex-column">
    <div class="flex flex-1 content">
      <div class="left-side">
        <ul class="flex flex-column">
          <li
            class="flex flex-y-center"
            v-for="(item,ind) in categoryList"
            :key="item.id"
            @click="categoryIndex=ind"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              :class="categoryIndex===ind ? 'icon svg-icon expand-icon active': 'icon svg-icon expand-icon'"
              viewBox="0 0 14 14"
              aria-hidden="true"
            >
              <path
                d="M.875 5.25V2.625c0-.483.392-.875.875-.875H7L8.75 3.5h3.5c.483 0 .875.392.875.875v.875H.875z"
                class="main"
              />
              <path
                d="M1.009 6.125H12.99a.875.875 0 0 1 .866.999l-.625 4.375a.875.875 0 0 1-.866.751H1.634a.875.875 0 0 1-.866-.751L.143 7.124a.875.875 0 0 1 .866-.999z"
                class="secondary"
              />
            </svg>
            <span :class="categoryIndex===ind ? 'active' : ''">{{item.title}}</span>
          </li>
        </ul>
      </div>
      <div class="right-side flex flex-1">
        <div
          class="item flex flex-column flex-y-center"
          v-for="item in list[categoryIndex]['child']"
          :key="item.id"
          @click="checkItem = item"
          @dblclick="confirmSelect(checkItem = item)"
        >
          <img
            :class="checkItem && checkItem.id===item.id ? 'active' : ''"
            :src="item.src"
            :alt="item.label"
            :title="item.label"
          />
          <span class="flex flex-x-center" :title="item.label">{{item.label}}</span>
        </div>
      </div>
    </div>
    <div class="bottom flex flex-y-center">
      <button @click="confirmSelect">确定</button>
      <button @click="closeDialog">取消</button>
    </div>
  </div>
</template>

<script>
import frz from '../../assets/yzk/frz.png'
import qygz from '../../assets/yzk/qygz.png'
import cwz from '../../assets/yzk/cwz.png'
import htz from '../../assets/yzk/htz.png'
import zdyz from '../../assets/yzk/zdyz.png'

export default {
  data () {
    return {
      categoryIndex: 0,
      checkItem: null,
      list: [
        {
          title: '默认分类', id: 1, child: [
            {
              id: 1,
              type: 'check_requisition',
              category: 'yzk',
              src: frz,
              label: '法人章',
              width: '95px',
              height: '95px',
              left: '95px',
              top: '95px',
              show: false,
              srcDataArr: []
            },
            {
              id: 2,
              type: 'travel_form',
              src: qygz,
              category: 'yzk',
              label: '企业公章',
              width: '95px',
              height: '95px',
              left: '95px',
              top: '95px',
              show: false,
              srcDataArr: []
            },
            {
              id: 3,
              type: 'triple_receipt',
              category: 'yzk',
              src: cwz,
              label: '财务章',
              width: '95px',
              height: '95px',
              left: '95px',
              top: '95px',
              show: false,
              srcDataArr: []
            },
            {
              id: 7,
              src: htz,
              type: 'transfer_cheque',
              category: 'yzk',
              label: '合同章',
              width: '95px',
              height: '95px',
              left: '95px',
              top: '95px',
              show: false,
              srcDataArr: []
            },
            {
              id: 9,
              label: '自定义章',
              width: '95px',
              height: '95px',
              left: '95px',
              top: '95px',
              src: zdyz,
              category: 'yzk',
              type: 'cash_node',
              show: false,
              srcDataArr: []
            }
          ]
        }
      ]
    }
  },
  computed: {
    categoryList () {
      let l = []
      if (this.list.length > 0) {
        this.list.forEach(v => {
          l.push({ title: v.title, id: v.id })
        })
      }
      return l
    }
  },
  methods: {
    // search () {
    //   alert('进行关键字搜索')
    // },
    closeDialog () {
      this.checkItem = null
      this.$parent.yzkDialogVisible = false
    },
    confirmSelect () {
      this.$parent.yinzhangDtat = this.checkItem
      // this.$emit('initClick')
      this.closeDialog()
    }
  }
}
</script>
<style lang="scss">
#yzk {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .tool {
    height: 40px;
    padding: 0 10px;
    border-bottom: 1px solid rgb(219, 219, 219);

    .tool-search {
      position: relative;

      svg {
        position: absolute;
        top: 5px;
        left: 5px;
      }

      input {
        height: 22px;
        padding-right: 22px;
        text-indent: 24px;
        color: rgb(65, 80, 88);
        background: rgb(255, 255, 255);
        border-width: 1px;
        border-style: solid;
        border-color: rgb(219, 219, 219);
        border-radius: 2px;
        transition: all 0.2s ease-out 0s;
      }
    }
  }

  .content {
    overflow: hidden;

    .left-side {
      width: 150px;
      height: 100%;
      overflow: auto;
      border-right: 1px solid #dbdbdb;

      ul {
        padding: 5px 10px;
        box-sizing: border-box;
        width: 100%;
        overflow: hidden;

        li {
          cursor: pointer;
          height: 25px;
          font-size: 12px;
          background: #fff;

          span.active {
            color: #298df8;
          }
        }
      }

      .svg-icon {
        flex-shrink: 0;
        margin-right: 4px;
        width: 14px;

        &.active {
          fill: #298df8;
        }
      }
    }

    .right-side {
      width: 0;
      padding: 20px;
      overflow: auto;
      flex-wrap: wrap;
      align-content: flex-start;

      .item {
        box-sizing: border-box;
        font-size: 12px;
        width: 120px;
        height: 100px;
        margin: 10px;
        overflow: hidden;
        cursor: pointer;
        position: relative;

        img {
          display: block;
          box-sizing: border-box;
          border: 2px solid #fff;
          width: 80px;
          height: 85px;
          overflow: hidden;

          &.active {
            border: 2px solid rgb(121, 186, 255);
            position: relative;
            filter: brightness(90%);
          }
        }

        span {
          width: 85px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          display: inline-block;
          height: 20px;
          line-height: 20px;
          text-align: center;
          margin-top: 5px;
        }
      }
    }
  }

  .bottom {
    border-top: 1px solid rgb(219, 219, 219);
    justify-content: flex-end;
    padding: 0 20px;
    height: 40px;

    button {
      cursor: pointer;
      min-width: 68px;
      height: 24px;
      margin-left: 10px;
      color: rgb(65, 80, 88);
      padding: 0 16px;
      background: rgb(255, 255, 255);
      border-width: 1px;
      border-style: solid;
      border-color: rgb(200, 205, 208);
      border-image: initial;
      border-radius: 2px;
      transition: all 0.2s ease-in-out 0s;

      &:hover {
        background: rgb(141, 158, 167);
      }
    }
  }
}
</style>
