<template>
  <div class="riJiZhang">
    <div class="title">
      <span>现金日记账</span>
      <span class="page font-12">
        第
        <input type="text" class="w-60" name="danju[]" style="position: relative;color: #666666;text-align: center;">
        <!-- <el-input v-model="initData.page" size="mini" name="danju[]" class="w-60" style="width: 60px!important;"></el-input> -->
        页
      </span>
    </div>
    <div class="content">
      <!--头-->
      <div class="content-header flex">
        <!--年月日-->
        <div class="w-70 flex flex-column">
          <div
            class="h-30 line-height-30 border-right-bottom-grey flex flex-justify-space-between pl-10 pr-10"
          >
            <el-input
              v-model="initData.year"
              size="mini"
              class="w-50"
              style="background-color: transparent;"
            ></el-input>
            <span>年</span>
          </div>
          <div class="flex-1 flex">
            <span
              class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey"
            >月</span>
            <span
              class="flex-1 flex flex-justify-center flex-align-center border-right-bottom-grey"
            >日</span>
          </div>
        </div>
        <!--凭证字号-->
        <div class="w-70 flex flex-column">
          <div
            class="h-30 line-height-30 border-right-bottom-grey flex flex-align-center flex-justify-center"
          >凭证</div>
          <div class="flex-1 flex">
            <span
              class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey"
            >字</span>
            <span
              class="flex-1 flex flex-justify-center flex-align-center border-right-bottom-grey"
            >号</span>
          </div>
        </div>
        <!--摘要-->
        <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey;">摘要</div>
        <!--选择-->
        <div class="w-60 flex flex-align-center flex-justify-center border-right-bottom-grey" style="border: 1px solid #E8E8E8">选择</div>
        <!--借方金额-->
        <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey">借方金额</div>
        <!--贷方金额-->
        <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey">贷方金额</div>
        <!--借或贷-->
        <!-- <div class="w-60 flex flex-align-center flex-justify-center border-right-bottom-grey">借或贷</div> -->
        <!--余额-->
        <div class="flex-1 flex flex-align-center flex-justify-center border-bottom-grey">余额</div>
      </div>
      <!--table-->
      <div class="content-table" style="background: #ffffff;">
        <div
          v-for="(item, index) in initData.table"
          :key="index"
          :class="['flex', 'h-36', 'position-relative', index + 1 === active?'bg-green':'']"
          @mouseover="mouseoverLine(index)"
          @mouseleave="mouseLeaveLine"
        >
          <div class="w-70 flex">
            <!--月-->
            <div
              class="flex-1 border-right-bottom-grey flex flex-justify-center flex-align-center center"
            >
              <el-input
                v-model="item.month"
                type="text"
                maxlength="2"
                size="small"
                style="top: -17px"
                name="danju[]"
              />
            </div>
            <!--日-->
            <div
              class="flex-1 border-right-bottom-grey flex flex-justify-center flex-align-center center"
            >
              <el-input
                v-model="item.day"
                type="text"
                maxlength="2"
                size="small"
                style="top: -17px"
                name="danju[]"
              />
            </div>
          </div>
          <div class="w-70 flex">
            <!--字-->
            <div
              class="flex-1 border-right-bottom-grey flex flex-justify-center flex-align-center center"
            >
              <el-input
                v-model="item.zi"
                type="text"
                maxlength="1"
                size="small"
                style="top: -17px"
                name="danju[]"
              />
            </div>
            <!--号-->
            <div
              class="flex-1 border-right-bottom-grey flex flex-justify-center flex-align-center center"
            >
              <el-input
                v-model="item.hao"
                type="text"
                maxlength="3"
                size="small"
                style="top: -17px"
                name="danju[]"
              />
            </div>
          </div>
          <!--摘要-->
          <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey">
            <el-input
              v-model="item.zhai_yao"
              type="text"
              size="small"
              style="top: -17px"
              name="danju[]"
            />
          </div>
          <!--选择-->
          <div class="w-60 flex flex-align-center flex-justify-center border-right-bottom-grey">
            <el-checkbox v-model="item.state" :true-label="1" :false-label="0" size="medium " />
          </div>
          <!--借方金额-->
          <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey">
            <el-input
              v-model="item.jie_money"
              type="text"
              size="small"
              style="top: -17px"
              name="danju[]"
            />
          </div>
          <!--贷方金额-->
          <div class="flex-1 flex flex-align-center flex-justify-center border-right-bottom-grey">
            <el-input
              v-model="item.dai_money"
              type="text"
              size="small"
              style="top: -17px"
              name="danju[]"
            />
          </div>
          <!--借或贷-->
          <!-- <div class="w-60 flex flex-align-center flex-justify-center border-right-bottom-grey center" style="border-right: 1px solid green;">
            <el-input v-model="item.jie_huo_dai" type="text" maxlength="3" size="small"/>
          </div>-->
          <!--余额-->
          <div class="flex-1 flex flex-align-center flex-justify-center border-bottom-grey">
            <el-input
              v-model="item.yu_e"
              type="text"
              maxlength="11"
              style="top: -17px"
              size="small"
              name="danju[]"
            />
          </div>
          <!--增行按钮-->
          <div v-show="index + 1 === active" class="add-line-icon-div">
            <i class="el-icon-circle-plus-outline add-line-icon" @click="addLine"></i>
          </div>
          <!--减少按钮-->
          <div v-show="index + 1 === active" class="remove-line-icon-div">
            <i class="el-icon-remove-outline remove-line-icon" @click="removeLine(index)"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RiJiZhang',
  data () {
    return {
      initData: {
        page: '',
        year: '',
        table: []
      },
      active: 0
    }
  },
  props: {
    riJiZhangAnswer: {
      type: Array,
      required: true,
      defaults: []
    }
  },
  created () {
    this.$nextTick(() => {
      this.init()
    })

  },
  methods: {
    // 初始化数据
    init () {
      if (this.riJiZhangAnswer.length === 0) {
        this.addLine()
      } else {
        let newArr = []
        for (let i = 0; i < this.riJiZhangAnswer[0].length / 8; i++) {
          newArr.push(this.riJiZhangAnswer[0].slice(i * 8, (i + 1) * 8))
        }
        for (let one of newArr) {
          let _t = {
            month: one[0],
            day: one[1],
            zi: one[2],
            hao: one[3],
            zhai_yao: one[4],
            jie_money: one[5],
            dai_money: one[6],
            yu_e: one[7]
          }

          this.initData.table.push(_t)
        }
      }
    },
    // 增行
    addLine () {
      this.initData.table.push({ month: '', day: '', zi: '', hao: '', zhai_yao: '', state: 0, jie_money: '', dai_money: '', jie_huo_dai: '', yu_e: '' })
    },
    // 删行
    removeLine (index) {
      if (this.initData.table[index].month !== '' || this.initData.table[index].day !== '' || this.initData.table[index].zi !== '' || this.initData.table[index].hao !== '' || this.initData.table[index].zhai_yao !== '' || this.initData.table[index].state === 1 || this.initData.table[index].jie_money !== '' || this.initData.table[index].dai_money !== '' || this.initData.table[index].jie_huo_dai !== '' || this.initData.table[index].yu_e !== '') {
        this.initData.table[index].month = ''
        this.initData.table[index].day = ''
        this.initData.table[index].zi = ''
        this.initData.table[index].hao = ''
        this.initData.table[index].zhai_yao = ''
        this.initData.table[index].state = 0
        this.initData.table[index].jie_money = ''
        this.initData.table[index].dai_money = ''
        // this.initData.table[index].jie_huo_dai = ''
        this.initData.table[index].yu_e = ''
      } else if (this.initData.table.length >= 4) {
        this.initData.table.splice(index, 1)
      }
    },
    mouseoverLine (index) {
      this.active = index + 1
    },
    mouseLeaveLine () {
      this.active = 0
    }
  }
}
</script>

<style lang="scss" scoped>
.riJiZhang {
  background: #fff;
  width: 100%;
  color: #333333;
  margin: 0 auto;
  padding: 20px;
  /*padding: 20px 45px;*/
  .title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 1px;
    position: relative;
    .page {
      position: absolute;
      right: 0;
      top: 10px;
    }
  }
  .content {
    margin-top: 35px;
    border-style: solid;
    border-color: #e8e8e8;
    border-width: 1px 1px 0 1px;
    .content-header {
      width: 100%;
      height: 65px;
      background: #f3f3f3;
      font-size: 13px;
    }
    .content-table {
      .bg-green {
        background-color: #e5f7d6;
      }
      .add-line-icon-div {
        position: absolute;
        height: 36px;
        line-height: 36px;
        width: 30px;
        left: -30px;
        text-align: center;
        top: 0;
      }
      .add-line-icon {
        font-size: 20px;
        color: #6dc3ea;
        cursor: pointer;
      }
      .remove-line-icon-div {
        position: absolute;
        height: 36px;
        line-height: 36px;
        text-align: center;
        width: 30px;
        right: -30px;
        top: 0;
      }
      .remove-line-icon {
        font-size: 20px;
        color: #f74c23;
        cursor: pointer;
      }
    }
  }
}
</style>
